import React from 'react'

const PButton = ({text, onSetPomoActivity, onChangeState, activity}) => {
    const handleButton = () => {
        onSetPomoActivity(text);
        onChangeState();
    }
    const handlePressed = () => {
        console.log("hi")
        if (activity.localeCompare(text) === 0) {
            return true
        }
        return false
    }
    return (
        <div className="pomo-btn">
            <button className={handlePressed() ? 'clicked': 'btn'} onClick={handleButton}>{text}</button>
 
        </div>
    )
}

export default PButton
