import React from 'react'
import Countdown from './Countdown'
import PomoBtns from './PomoBtns'
import { useState } from 'react'

const Pomodoro = () => {
    const [pomoActivity, setPomoActivity] = useState('Pomodoro');
 
    const setChanges = () => {
        setPomodoroActivity(); 
    }

    const setPomodoroActivity = () => {
        console.log(pomoActivity)
        if (pomoActivity.localeCompare('Pomodoro') === 0) {
            return <Countdown key="Pomodoro" minutes={25} seconds={0}></Countdown>
        } else if (pomoActivity.localeCompare('Short Break') === 0) {
            return <Countdown key="Short Break" minutes={5} seconds={0}/>
        } else if (pomoActivity.localeCompare('Long Break') === 0){
            return <Countdown key="Long Break" minutes={15} seconds={0}/>
        }
    }

    return (
        <div>
            {console.log("rendering")}
            <PomoBtns onSetPomoActivity={setPomoActivity} onChangeState={setChanges} onActivity={pomoActivity}></PomoBtns>
            {setPomodoroActivity()}
            {/* {!pomoActivity.localeCompare('Pomodoro')? changeTimer(25, 0) : changeTimer(5, 0)} */}
            
            
            
        </div>
    )
}

export default Pomodoro
