import React from 'react'



const TodoAdd = ({todos, setTodos}) => {
    let uniqueId = todos.length;
    const getUniqueId = () => uniqueId++;

    
    const setTodoLocalStorage = () => {
        window.localStorage.setItem('todos', JSON.stringify(todos));
    }
    const addNewTodo = (todo) => {
        console.log(todos)
        let newTodo = {
            id: getUniqueId(),
            content: todo,
            finished: false


        }
        console.log(JSON.stringify(todos))
        setTodos([...todos, newTodo]);


    }

    const handleKeyDown = (e) => {
        if (e.key === "Enter") {

            addNewTodo(e.target.value);
            setTodoLocalStorage();
            e.target.value = '';

        }
    }
    return (
        <div className="search">
            <input type="text" placeholder="Buy Groceries" onKeyDown={handleKeyDown} ></input>
            
        </div>
    )
}

export default TodoAdd
