import React from 'react'
import { useState, useEffect} from 'react'

const Countdown = ({minutes = 0, seconds = 0}) => {
    const [timer, setTimer] = useState({
        m: parseInt(minutes),
        s: parseInt(seconds)
    });
    const [finished, setFinised] = useState(false);
    const [paused, setPause] = useState(false);
    const [start, setStart] = useState(false)

    const calculateTime = () => {
        if (paused || finished || !start) {
            return;
        }

        if (timer.m === 0 && timer.s === 0) {
            setFinised(true);
        } else if (timer.s === 0) {
            setTimer({
                m: timer.m - 1,
                s: 59
            })
        } else {
            setTimer({
                m: timer.m,
                s: timer.s - 1

            })

        }      
    };

    const handleReset = () => {
        setTimer({
            m: parseInt(minutes),
            s: parseInt(seconds)
        })
        setFinised(false);
        setPause(false);
        setStart(false);
    }

    const handleStart = () => {
        setStart(true)
    }

    useEffect(() => {
        const timerInterval = setInterval(() => calculateTime(), 1000);
        return () => clearInterval(timerInterval);
    })
    return (
        <div>
            <h1>{timer.m}:{timer.s < 10 ? 0:''}{timer.s}</h1>
            {!start ?  <button className="btn" onClick={() => handleStart()}>Start</button> :
             <div className="btn-group">
                <button className="btn" onClick={() => setPause(!paused)}>{paused ? 'Resume': 'Pause'}</button>
                <button className="btn" onClick={() => handleReset()}>Reset</button>
            </div>}     
        </div>
    )
}

export default Countdown
