import React from 'react'
import PButton from './PButton'
const PomoBtns = ({onSetPomoActivity, onChangeState, onActivity}) => {
 
    return (
        <div className="btn-group pomo">
            <PButton text="Pomodoro" onSetPomoActivity={onSetPomoActivity} onChangeState={onChangeState} activity={onActivity}></PButton>
            <PButton text="Short Break" onSetPomoActivity={onSetPomoActivity} onChangeState={onChangeState} activity={onActivity}></PButton>
            <PButton text="Long Break" onSetPomoActivity={onSetPomoActivity} onChangeState={onChangeState} activity={onActivity}></PButton>
        </div>
    )
}

export default PomoBtns
